import { vec3 } from 'gl-matrix';
import type { Types } from '@cornerstonejs/core';
import type { InitializedOperationData } from '../BrushStrategy';
import StrategyCallbacks from '../../../../enums/StrategyCallbacks';

/**
 * Adds an isTargetSegment to the operation data that checks that the
 * image segment is the selected one
 * No-op if target segment not defined.
 */
export default {
  [StrategyCallbacks.CreateIsTargetSegment]: (
    operationData: InitializedOperationData
  ) => {
    const { segmentationVoxelManager, strategySpecificConfiguration } =
      operationData;
    if (!strategySpecificConfiguration) {
      return;
    }
    return (index) => {
      const { REPLACE, REPLACE_INSIDE_CIRCLE } =
        strategySpecificConfiguration;

      const segmentIndex = segmentationVoxelManager.scalarData[index];

      // Prefer the generic version of the REPLACE configuration, but fallback
      // to the older REPLACE_INSIDE_CIRCLE version.
      const { targetSegmentIndex } = REPLACE || REPLACE_INSIDE_CIRCLE || {};
      if (targetSegmentIndex === undefined) {
        return true;
      }
      return targetSegmentIndex === segmentIndex;
    };
  },
};
