import { ImageVolume, eventTarget } from '@cornerstonejs/core';
import type { Types } from '@cornerstonejs/core';
import { Events } from '../../../enums';
import { setSegmentationDirty } from '../../../utilities/segmentation/getUniqueSegmentIndices';

type OperationData = {
    segmentationId: string;
    volume: ImageVolume;
    oldScalarData: ImageVolume;
};

/**
 * Simply replace the current segmentation data by the oldScalarData put in parameter
 * @param enabledElement
 * @param operationData
 */
export function fillReplace(
    enabledElement: Types.IEnabledElement,
    operationData: OperationData,
): void {
    const { volume: segmentation, segmentationId, oldScalarData } = operationData;

    const scalarData = segmentation.getScalarData();

    for(let i = 0; i < scalarData.length; i++){
        scalarData[i] = oldScalarData[i]
    }

    // Code copied from triggerSegmentationDataModified
    // This way, the same actions are done without triggering segmentationDataModified event
    setSegmentationDirty(segmentationId);
    const event = new CustomEvent(Events.SEGMENTATION_DATA_MODIFIED, { detail: { segmentationId }, cancelable: true });
    eventTarget.dispatchEvent(event);
}
